// API_PATH
import {
  API_PATH
} from '@/config'

// ajax
import ajax from '@/utils/ajax'

/**
 * 停电信息发布监控
 * @const
 * @type {String}
 * @default  '[API_PATH]/workstation/blackoutReleaseMonitoring'
 */
export const GET_BLACKOUT_RELEASE_MONITORING = API_PATH + '/workstation/blackoutReleaseMonitoring'

/**
 * 停电信息发布监控
 * @param {object} [data] 发送键值对数据
 * @returns {promise}
 */
export function getBlackoutReleaseMonitoring(data) {
  return ajax({
    method: 'POST',
    headers: {
      "Content-Type": "application/x-www-form-urlencoded"
    },
    data: data,
    url: GET_BLACKOUT_RELEASE_MONITORING
  })
}

