<template>
  <div class="line-charts">
    <div :id="id" class="charts"></div>
  </div>
</template>

<script>
import echarts from 'echarts';

/**
 * @参数说明：
 * 必须：chartData:{
     *       dimension: [],  X轴的信息
     *       实际数据的展示数据。对象的属性为：统计的维度信息名；属性值为：对应x轴的信息的各个数值信息。
            dataMap: {
                ‘本月’:[12,45,66,90,99,88],
                ‘上月’:[23,45,77,88,89,100]
            },
            维度的颜色展示信息
            color: ['#0088d1', '#df896d']
     * }
 * xRotate：x轴信息的文字旋转角度。非必须。
 * yDw： y轴（数据值）的单位，如 %，万，千万 等
 * showLegend ： 是否显示图例，默认显示,
 * maxPercent :数值是否最大为100%,默认为否,不设置最大值
 * */
export default {
  name: "bda-line-charts",
  props: {
    chartData: {
      type: Object,
      default: () => {
        return {}
      }
    },
    xRotate: {
      type: Number,
      default: () => {
        return 0
      }
    },
    yDw: {
      type: String,
      default: () => {
        return ''
      }
    },
    nameXs: {
      type: String,
      default: () => {
        return ''
      }
    },
    showLegend: {
      type: Boolean,
      default: () => {
        return true
      }
    },
    maxPercent: {
      type: Boolean,
      default: () => {
        return false
      }
    }
  },
  data() {
    return {
      id: Math.random() + '_',
      lineData: [],
      xData: [],
      legend: [],
      series: []
    }
  },
  watch: {},
  created() {
    this.xData = this.chartData.dimension ? this.chartData.dimension : [];

  },
  mounted() {
    this.dataFilter(this.chartData);
    this.init();
  },
  methods: {
    dataFilter(data) {
      let series = [];
      let color = data.color;
      if (data.dataMap) {
        let count = -1;
        for (let key in data.dataMap) {
          if (key != '及时率') {
            this.legend.push(key);
            count++;
            this.lineData.push(data.dataMap[key]);
            series.push({
              name: key,
              type: 'line',
              symbol: 'circle',
              symbolSize: 8,
              smooth: true,
              areaStyle: {
                normal: {
                  color: new echarts.graphic.LinearGradient(
                    0, 0, 0, 1,
                    [
                      { offset: 0, color: color[count] },
                      { offset: 0.4, color: color[count] },
                      { offset: 1, color: 'rgba(128, 128, 128, 0)' }
                    ]
                  )
                }
              },
              itemStyle: {
                color: color[count],
                shadowColor: color[count],
                shadowBlur: 20,
                normal: {
                  color: {
                    type: 'radial',
                    x: 0.5,
                    y: 0.5,
                    r: 0.5,
                    colorStops: [{
                      offset: 0, color: '#fff' // 0% 处的颜色
                    },
                    {
                      offset: 0.2, color: '#fff' // 0% 处的颜色
                    },
                    {
                      offset: 1, color: color[count] // 100% 处的颜色
                    }],
                    global: false // 缺省为 false
                  },
                  shadowColor: color[count],

                  lineStyle: {
                    color: color[count],
                    shadowColor: color[count],
                    shadowOffsetX: 0,
                    shadowOffsetY: 0,
                    opacity: 1,
                    shadowBlur: 8,
                    type: "solid",
                    width: 4
                  },
                }
              },
              data: this.lineData[count]
            });
          }
        }
      }
      this.series = series;
    },
    init() {
      let chartsDom = document.getElementById(this.id);
      let charts = echarts.init(chartsDom);
      let labelx = {
        color: '#fff',
        interval: 3,
        showMaxLabel: true,
        align: 'right',
      };
      if (this.xRotate !== 0) {
        labelx.rotate = this.xRotate;
      }

      let dw = this.yDw;
      let xs = this.nameXs;
      let options = {
        grid: {
          left: 5,
          right: 10,
          top: '10%',
          bottom: '5%',
          containLabel: true
        },
        tooltip: {
          show: true,
          trigger: 'axis',
          formatter: function (a) {
            let info = '';
            let _i = 0;
            for (let item of a) {
              _i++;
              if (a.length > 1) {
                info += item.seriesName
              }
              info += item.axisValueLabel + xs + '：' + item.value.toLocaleString() + dw;
              if (_i < a.length) {
                info += '<br>'
              }
            }
            return info;
          }
        },
        legend: {
          color: this.chartData.color,
          show: this.showLegend,
          x: 'right',
          icon: 'stack',
          itemWidth: 10,
          itemHeight: 10,
          textStyle: {
            fontFamily: 'Noto Sans SC',
            color: '#fff',
          },
          data: this.legend
        },
        xAxis: [
          {
            type: 'category',
            boundaryGap: false,
            axisLabel: labelx,
            axisLine: {
              show: true,
              lineStyle: {
                color: '#fff',
                fontFamily: 'Noto Sans SC',

              }
            },
            axisTick: {
              show: true,
            },
            splitLine: {
              show: false,
              lineStyle: {
                color: '#fff'
              }
            },
            data: this.xData
          }
        ],
        yAxis: [
          {
            type: 'value',
            name: '',
            axisLabel: {
              formatter: '{value}' + dw,
              interval: 1,
              textStyle: {
                color: '#fff',
                fontFamily: 'Noto Sans SC',
              }
            },
            axisLine: {
              lineStyle: {
                color: '#fff'
              }
            },
            axisTick: {
              show: true,
            },
            splitLine: {
              show: true,
              lineStyle: {
                color: 'rgba(255,255,255,.1)'
              }
            }
          }
        ],
        series: this.series
      };
      if (this.maxPercent) options.yAxis[0].max = 100

      // window.console.log(options, 'options');
      charts.setOption(options)
    }
  }
}
</script>

<style scoped lang="scss">
.line-charts {
  width: 100%;
  height: 100%;
  position: relative;

  .charts {
    width: 100%;
    height: 100%;
  }
}
</style>