<!-- 饼图 -->
<template>
  <div class="coutLabel" :style="'justify-content:' + textAlign + ';'">
    <div
      v-for="item in countData"
      :key="item.key"
      :title="item.showText + ' ' + item.value + ' ' + countUnit"
      :class="{ active: countData.length > 1 }"
      :style="{ fontSize: fontSize + 'px' }"
      class="item"
    >
      <!-- {{item.showText+' '+item.value+' '+countUnit}} -->
      <span class="show">{{ item.showText + "：" }}</span>
      <span class="value" :style="{ color: item.color }">{{
        item.value + countUnit
      }}</span>
    </div>
  </div>
</template>

<script>

/**
   * @ 统计信息组件
   * @risheng.huang
   * @参数说明：
   * countData[
   *  key: '0',//状态
   *  value: '2345',//该状态下的值
   *  showText: '业务量' //所代表的中文意思
   * ]//数组第一项应该是总量,其它按状态依次排放
   * countUnit:'件' //显示的单位
   * fontSize:16
   * */


export default {
  name: "coutLabel",
  props: {
    countData: {
      type: Array,
      required: true,
    },
    countUnit: {
      type: String,
      required: true,
    },
    fontSize: {
      type: Number,
      default: 16,
    },
    textAlign: {
      type: String,
      default: 'center'
    }
  },
  watch: {
    countData: {//深度监听，可监听到对象、数组的变化
      handler(newData) {
        this.countData = newData;
        this.countData.forEach((item, index) => {
          // if (item.value>=1000){
          //     item.value = (item.value/1000).toFixed(2)+'K'
          // }
          if (item.showText == '业务量') {
            this.countData.splice(index, 1)
          }
        })
      },
      deep: true, //true 深度监听
      immediate: true //开启首次监听赋值（不开启首次监听不到）
    },
  },
  data() {
    return {

    }
  },
  mounted() {

  },
  methods: {

  }
}
</script>

<style lang="scss" scoped>
.coutLabel {
  //  display: flex;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  .item {
    display: inline-block;
    // width: 35%;
    // flex: 1;
    span {
      cursor: pointer;
      width: auto;
      // padding: 0px 1px;
      display: inline-block;
      text-align: center;
      height: 20px;
      margin: 1px 3px 1px 0px;
      // font-size: 16px;
      /*background-color: rgba(29, 80, 137, 0.5);*/
      /*border: 1px solid #203270;*/
      // box-shadow: rgb(32, 50, 112) 0px 0px 10px inset;
      color: #fff;
    }
  }

  // .active {
  //   /*width: calc(100% - 6px);*/
  //   white-space: nowrap;
  //   overflow: hidden;
  //   text-overflow: ellipsis;
  // }
}
</style>