<template>
  <div class="remote-bottom-style">
    <div class="remote-style">
      <div class="remote-style-top">
        <div
          class="tj-main"
          v-loading="phoneCountRes"
          element-loading-background="rgba(0, 0, 0, 0.5)"
          element-loading-text="加载中"
        >
          <div class="title">
            <!--                    <i class="el-icon-phone-outline"></i> -->
            <span>年电话量</span>
          </div>
          <div class="tj-all-count">
            {{ yearPhonesCount.callVolumeForYear }}
          </div>
          <div class="kb-count">
            <div class="title-tr">
              <span>最高月电话量</span>
            </div>
            <div class="value-tr">
              <span>{{
                dateTranslate(
                  "mounth",
                  yearPhonesCount.maxCallVolumeForMonth.callday
                )
              }}</span>
              <span>{{
                parseInt(yearPhonesCount.maxCallVolumeForMonth.ccsum)
              }}</span>
            </div>
            <div class="title-tr">
              <span>最高日电话量</span>
            </div>
            <div class="value-tr" style="color: #86ff80">
              <span>{{
                dateTranslate(
                  "date",
                  yearPhonesCount.maxCallVolumeForDay.callday
                )
              }}</span>
              <span>{{
                parseInt(yearPhonesCount.maxCallVolumeForDay.ccsum)
              }}</span>
            </div>
          </div>
        </div>
        <div
          class="charts-main border-style"
          v-loading="!mounthPhoneRes"
          element-loading-background="rgba(0, 0, 0, 0.5)"
          element-loading-text="加载中"
        >
          <div class="title">月电话量</div>
          <div>
            <countLabel
              v-if="mounthPhoneRes"
              :countData="mounthLabel"
              :countUnit="phonesUnit"
            ></countLabel>
          </div>
          <div class="charts-style">
            <bda-line-charts
              v-if="mounthPhoneRes"
              :chartData="mounthPhoneData"
              :nameXs="mounthNamexs"
            ></bda-line-charts>
          </div>
        </div>
      </div>
      <div class="remote-style-bottom">
        <div
          class="charts-main1 border-style"
          v-loading="!dayPhoneRes"
          element-loading-background="rgba(0, 0, 0, 0.5)"
          element-loading-text="加载中"
        >
          <div class="title">日电话量</div>
          <div>
            <countLabel
              v-if="dayPhoneRes"
              :countData="dayLabel"
              :countUnit="phonesUnit"
            ></countLabel>
          </div>
          <div class="charts-style">
            <bda-line-charts
              v-if="dayPhoneRes"
              :chartData="dayPhoneData"
              :nameXs="dayNamexs"
            ></bda-line-charts>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Interface from '../../../base/mixin/remote-work-monitoring';
import qs from 'qs';
import bdaLineCharts from '../../../widgets/bda-line-charts/bda-line-charts';
import countLabel from "@/components/countLabel/countLabel";

export default {
  name: "remoteLeft",
  mixins: [Interface],
  components: {
    bdaLineCharts, countLabel,
  },
  data() {
    return {
      yearPhonesCount: {
        callVolumeForYear: 0,
        maxCallVolumeForMonth: {
          ccsum: 0,
          callday: ''
        },
        maxCallVolumeForDay: {
          ccsum: 0,
          callday: ''
        }
      },
      mounthPhoneRes: false,
      mounthPhoneData: {},
      mounthNamexs: '日',
      dayPhoneRes: false,
      dayPhoneData: {},
      dayNamexs: '时',
      mounthLabel: [],
      dayLabel: [],
      phonesUnit: '',
      phoneCountRes: true,
      businessCountRes: true

    }
  },
  watch: {},
  computed: {},
  created() {

  },
  mounted() {
    if (this.$parent.dateParams) {
      this.getCallVolumeForYear(qs.stringify({
        timeStr: this.$parent.dateParams
      })).then(res => {
        this.yearPhonesCount = res;
        this.phoneCountRes = false;
      }).catch(() => {
        this.phoneCountRes = false;
      });

      this.getCallVolumeForMonth(qs.stringify({
        timeStr: this.$parent.dateParams
      })).then(res => {

        this.callVolumeCallback(res, 'month');
      }).catch(() => {
        this.mounthPhoneRes = false;
      });
      this.getCallVolumeForDay(qs.stringify({
        timeStr: this.$parent.dateParams
      })).then(res => {
        this.callVolumeCallback(res, 'day');
      }).catch(() => {
        this.dayPhoneRes = false;
      });
    }


  },
  methods: {
    dateTranslate(type, dateStr) {
      let date = "";
      if (dateStr) {
        let str = ''
        if (dateStr.length === 6) {
          str = dateStr.substring(0, 4) + '/' + dateStr.substring(4, 6) + '/01'
        } else if (dateStr.length === 8) {
          str = dateStr.substring(0, 4) + '/' + dateStr.substring(4, 6) + '/' + dateStr.substring(6, 8)
        }
        if (str) {
          let dqDate = new Date(str);
          if (type === 'mounth') {
            date = (dqDate.getMonth() + 1) + '月'
          } else {
            date = (dqDate.getMonth() + 1) + '月' + dqDate.getDate() + '日'
          }
        }

      }
      return date;
    },
    callVolumeDataFilter(data, key1, key2, leg1, leg2, xKey) {
      let xData = [], dataMap = {};
      dataMap[leg1] = [];
      dataMap[leg2] = [];
      let status = '';
      if (data) {
        let beforMonthLen = data[key1] ? data[key1].length : 0;
        let presentMonthLen = data[key2] ? data[key2].length : 0;
        // 判读哪个数据长度较长，则作为x轴的维度信息
        if (beforMonthLen - presentMonthLen >= 0) {
          status = 'before'
        } else {
          status = 'present'
        }
        if (beforMonthLen > 0) {
          for (let item of data[key1]) {
            if (item[xKey]) {
              let disLength = item[xKey].length;
              let name = item[xKey].substring(disLength - 2, disLength);
              item.value = item.ccsum;
              item.name = name;
              if (status === 'before') {
                xData.push(name);
              }
            }
          }
        }
        if (presentMonthLen > 0) {
          for (let item of data[key2]) {
            if (item[xKey]) {
              let disLength = item[xKey].length;
              let name = item[xKey].substring(disLength - 2, disLength);
              item.value = item.ccsum;
              item.name = name;
              if (status === 'present') {
                xData.push(name);
              }
            }
          }
        }
      }
      dataMap[leg1] = data[key1];
      dataMap[leg2] = data[key2];
      return {
        dataMap: dataMap,
        xData: xData
      }
    },
    callVolumeCallback(data, type) {
      let mapData = {}, countMap = [];
      if (data) {
        if (type === 'month') {
          countMap.push({
            key: Math.random() + '',
            value: data.callVolumeForBeforeMonthValue !== undefined ? data.callVolumeForBeforeMonthValue : 0,
            showText: '上月电话量',
            color: '#019eff',
          });
          countMap.push({
            key: Math.random() + '',
            value: data.callVolumeForMonthValue !== undefined ? data.callVolumeForMonthValue : 0,
            showText: '本月电话量',
            color: '#00FFD4',
          });

          let _filterData = this.callVolumeDataFilter(data, 'callVolumeForBeforeMonthLine', 'callVolumeForMonthLine', '上月', '本月', 'callday');
          mapData = {
            dimension: _filterData.xData,
            dataMap: _filterData.dataMap,
            color: ['#019eff', '#00FFD4']
          };
          this.mounthPhoneRes = false;
          this.mounthPhoneData = mapData;
          this.mounthLabel = countMap;
          this.$nextTick(() => {
            this.mounthPhoneRes = true;
          })
        } else {

          countMap.push({
            key: Math.random() + '',
            value: data.callVolumeForBeforeDayValue !== undefined ? data.callVolumeForBeforeDayValue : 0,
            showText: '昨日电话量',
            color: '#019eff',
          });
          countMap.push({
            key: Math.random() + '',
            value: data.callVolumeForDayValue !== undefined ? data.callVolumeForDayValue : 0,
            showText: '今日电话量',
            color: '#00FFD4',
          });

          let _filterData = this.callVolumeDataFilter(data, 'callVolumeForBeforeDayLine', 'callVolumeForDayLine', '昨日', '今日', 'callhour');
          mapData = {
            dimension: _filterData.xData,
            dataMap: _filterData.dataMap,
            color: ['#019eff', '#00FFD4']

          };
          this.dayPhoneRes = false;
          this.dayPhoneData = mapData;
          this.dayLabel = countMap;
          this.$nextTick(() => {
            this.dayPhoneRes = true;
          })
        }
      }
    },

  }
}
</script>

<style scoped lang="scss">
