      position: relative;

      .title {
        // background: linear-gradient(
        //   to bottom,
        //   rgba(15, 146, 229, 0.3),
        //   #061b3a
        // );
        // margin-bottom: 20px;
        // line-height: 50px;
        height: 19%;
        color: #00e7ff;
        // letter-spacing: 3px;
        display: flex;
        justify-content: center;
        align-items: center;
        // span {
        //   top: 8%;
        //   position: absolute;
        //   left: 36%;
        // }
      }

      .tj-all-count {
        // margin-bottom: 15px;
        // line-height: 55px;
