      }
    }

    .charts-main,
    .charts-main1 {
      flex: 2.5;
      height: 100%;
      padding: 10px;
      box-sizing: border-box;
      float: left;
      position: relative;
      background: url("../../../assets/img/远程框2.png") no-repeat top center;
      background-size: 100% 100%;
      .title {
        // background: linear-gradient(
        //   to bottom,
        //   rgba(15, 146, 229, 0.3),
        //   #061b3a
        // );
        color: #00e7ff;
        // line-height: 38px;
        height: 18%;
        display: flex;
        justify-content: center;
        align-items: center;
      }

      .charts-style {
        width: 100%;
        padding: 0 20px;
        box-sizing: border-box;
        height: calc(82% - 40px);
        // height: 100%;
        position: relative;
      }
    }
    .charts-main1 {
      //   flex: 2;
      //   height: 50%;
      background: url("../../../assets/img/远程框3.png") no-repeat top center;
      background-size: 100% 100%;
